export interface IConverter{
    name:string;
    convert(text:string):string;
}
export class NoConverter implements IConverter{
    name : string="NoConvertConverter";
    convert(text:string):string{
        return text;
    }
}

export class ToBase64Converter implements IConverter{
        name : string="ToBase64ConvertConverter";
        convert(text:string):string{
            return btoa(text);
        }
}

export class FromBase64Converter implements IConverter{
    name : string="FromBase64ConvertConverter";
    convert(text:string):string{
        return atob(text);
    }
}


