import { Component } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

import * as Converters from './converter'

@Component({
  selector: 'text-converter-app',
  templateUrl: './app.main.html',
  styleUrls: ['./app.main.css']
})
export class AppMain {
  inputText: string = '';

  current_converter: Converters.IConverter;

  converters: Array<Converters.IConverter> = [new Converters.NoConverter, new Converters.ToBase64Converter, new Converters.FromBase64Converter];

  constructor() {
    this.current_converter = this.converters[1];
  }

  getConvertedText(): string {
    return this.current_converter.convert(this.inputText);
  };


  onConverterSelect(converter: Converters.IConverter): void {
    this.current_converter = converter;
  }
}



