import { HostListener, Input, ElementRef, Directive, OnChanges, SimpleChanges } from '@angular/core';

@Directive({
  selector: 'textarea[textarea-autoheight]'
})

export class TextareaAutoheightDirective implements OnChanges {
  ngOnChanges(changes: SimpleChanges): void {
    this.updateHeight();
  }
  private el: HTMLElement;

  @Input() textare_autoheight_change_track: any;

  updateHeight() {
    this.el.style.overflow = 'hidden';
    this.el.style.height = 'auto';
    this.el.style.height = this.element.nativeElement.scrollHeight + 'px';
  }

  constructor(public element: ElementRef) {
    this.el = element.nativeElement;
    this.updateHeight();
  }

  @HostListener('input', ['$event.target'])
  onInput(textArea: HTMLTextAreaElement): void {
    this.updateHeight();
  }
}



